@font-face {
    font-family: 'Proxima Nova';
    src: url('./proximanova_regular.ttf') format('truetype');
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: sans-serif;
    color: white;
    background: #1a1527;
}

h1 {
    font-size: 36px;
}

p {
    font-size: 16px;
    font-weight: 400px;
}

h1, p {
    font-family: 'Proxima-nova', sans-serif;
    color: white;
}

li {
    list-style-type: none;
}

